.projects{
    background-color:  var(--bg-light);
    color: white;
    padding: 5% 8%;
}
.projects .project-container{
    /* border: 1px solid green; */
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}
.project-container .myProject{
    /* border: 2px dashed darkmagenta; */
    flex-basis: 30%;
    background-color: var(--bg-dark);
    box-shadow: 1px 0px 10px 0px rgba(0, 0, 0);
    cursor: pointer;
    /* border-radius: 10px; */
}
.project-container .myProject:hover{
    /* border: 2px dashed darkmagenta; */
    transform: translateY(-5px);
    transition: 0.5s ease;
    box-shadow: 1px 0px 10px 3px rgba(0, 0, 0);

}
.myProject .img-container{
    /* border: 1px dashed lawngreen; */
    width: 100%;
    height: 200px;
    position: relative;
}
.img-container:after {
    content: "";
    position: absolute;
    left: 0; right: 0;
    top: 0; bottom: 0;
    /* background: rgba(0,200,0,.5); */
    background: var(--bg-light);
    opacity: 0.5;
    
}
.myProject .img-container img{
    /* border: 1px dashed lawngreen; */
    width: 100%;
    height: 100%;
    /* border-radius: 10px; */
    object-fit: cover;
}
